}

.menu-button {
    color: #ffffff;
    margin-left: 1.5em;
}

.header-text {
    font-size: 2.8em;
    font-family: "Playfair Display", serif;
    color: #ffffff;
    line-height: 1.15;
    margin-bottom: 1em;
}
 
.body-text {
    font-size: 1.5em;
    color: #ffffff;
    margin-left: 0.1em;
}

.color-text {
    color: #ffffff;
}

.header-box {
    margin-left: 3.5em;
    margin-top: 7.0em;
}
#iphone_header {
	position: absolute;
	left: 38em;
	bottom: 3em;
    width: 48%;
    height: auto;
	-webkit-animation: float 6s ease-in-out infinite;
	        animation: float 6s ease-in-out infinite;
	z-index: -1;
}

/* .iphone-box {
	text-align: center;
} */

@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px) rotate(0deg);
		        transform: translatey(0px) rotate(0deg);
	}
	50% {
		-webkit-transform: translatey(-10px) rotate(-3deg);
		        transform: translatey(-10px) rotate(-3deg);
	}
	100% {
		-webkit-transform: translatey(0px) rotate(0deg);
		        transform: translatey(0px) rotate(0deg);
	}
}

@keyframes float {
	0% {
		-webkit-transform: translatey(0px) rotate(0deg);
		        transform: translatey(0px) rotate(0deg);
	}
	50% {
		-webkit-transform: translatey(-10px) rotate(-3deg);
		        transform: translatey(-10px) rotate(-3deg);
	}
	100% {
		-webkit-transform: translatey(0px) rotate(0deg);
		        transform: translatey(0px) rotate(0deg);
	}
}


main {
  display: flex;
  height: 100vh;
}
